@import "../../inc/vars";
.acro-subscription-form {
	padding: 48px 30px;
	text-align: center;
	position: relative;
	background-color: #F7F3FF;
	overflow: hidden;
	@media (min-width: $screen-sm){
		&:before {
			content: '';
			position: absolute;
			background: url('/wp-content/themes/acro/images/ppc-landing/footer-lines.svg');
			animation: scale-up 6s ease-in-out infinite;
			width: 1075px;
			height: 643px;
			left: -50%;
			top: -90%;
		}
	}
	@media (min-width: $screen-sm){
		padding: 80px 30px;
	}
	.acro-subscription-form__wrapper {
		position: relative;
		z-index: 2;
		h2 {
			@include titleGrad();
			@include pop(400, 35, 1.24);
			letter-spacing: -0.7px;
			margin-bottom: 24px;
			@media (min-width: $screen-sm){
				@include pop(400, 53, 1.24);
				letter-spacing: -1.06px;
				margin-bottom: 16px;
			}
		}
		h5 {
			color: $purple;
			@include pop(400, 18, 1.24);
			margin-bottom: 24px;
			@media (min-width: $screen-sm){
				@include pop(400, 23, 1.36);
				margin-bottom: 16px;
			}
		}
		.description {
			@include fig(400, 20, 1.24);
			color: #4F4B58;
			text-align: center;
			margin-bottom: 24px;
			@media (min-width: $screen-sm){
				line-height: 156%;
				margin-bottom: 40px;
			}
		}
		.hbspt-form {
			form {
				display: flex;
				flex-direction: column;
				position: relative;
				width: 600px;
				max-width: 100%;
				margin: 0 auto;
				label {
					display: none;
				}
				input[type="email"]{
					border: none;
					background: #fff;
					display: flex;
					flex-direction: column;

					width: 100%;
					border-radius: 6px;
					box-shadow: 0px 12px 24px 0px rgba(27, 59, 119, 0.05), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 2px 4px 0px rgba(7, 4, 146, 0.10);
					height: 112px;
					padding-bottom: 56px;
					padding-left: 16px;
					padding-right: 16px;
					@media (min-width: $screen-sm){
						height: 56px;
						padding: 0 200px 0 16px;
					}
				}
				input[type="submit"]{
					@include greenButton();
					border: none;
					cursor: pointer;
					height: 48px;
					position: absolute;
					right: 4px;
					left: 4px;
					top: 56px;
					width: calc(100% - 8px);
					@media (min-width: $screen-sm){
						width: 186px;
						right: 4px;
						top: 4px;
						left: auto;
					}
				}
			}
		}
	}
}

@keyframes scale-up {
	0% {
		left: -30%;
		top: -90%;
		opacity: 1;
	}
	50% {
		left: -30%;
		top: -20%;
		opacity: 0.2;
		transform: scale(1.3);
	}
	100% {
		left: -30%;
		top: -90%;
		opacity: 1;
	}
}